import React, {Component} from 'react';
import APISerivce from '../../services/ApiService';
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CForm,
  CFormGroup,
  CInput,
  CLabel,
  CSelect,
  CInputCheckbox,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react';
import axios from 'axios';

class BasicForms extends Component {
    constructor(props) {
        super(props);
        this.state = {
            customer_list: [],
            plan_list: [],
            gst_list: [],
            gst: 0,
            customer_id:'', 
            plan_name: '',
            plan_doj: '',
            instllation_list: [], 
            plan_amount: 0, 
            router_list: [],
            installation_charge: 0,
            router_charge: 0,
            plan_gst: 0,
            router_gst: 0,
            installation_gst: 0,
            advance_installationamount: 0, 
            advance_planamount: 0, 
            advance_routeramount: 0,
            discount: 0,
            subtotal: 0,
            total: 0,
            bill: 0,
            if_router_available: false,
        }
    }

    componentDidMount() {
      const reqUser = APISerivce.get(`/listuser`);
      const reqPlan = APISerivce.get(`/listplan`);
      const reqGst = APISerivce.get(`/listgst`);
      const reqInstall = APISerivce.get(`/listinstallation`);
      const reqRouter = APISerivce.get(`/listroutercharges`);
      axios.all([reqUser, reqPlan, reqGst, reqInstall, reqRouter]).then(axios.spread((...response)=>{
        const resUser = response[0];
        const resPlan = response[1];
        const resGst = response[2];
        const resInstall = response[3];
        const resRouter = response[4]
        this.setState({
          customer_list: resUser.data,
          plan_list: resPlan.data.results,
          gst_list: resGst.data.results,
          instllation_list: resInstall.data.results,
          router_list: resRouter.data.results

        })
        console.log("aa",resUser,resPlan)
      })).catch(err=>{
        console.log('err',err)
      })
      
    }

    handleCancel = ()=> {
        this.props.history.push('/connections')
    }

    handleSubmit = () => {
      const { customer_id, plan_name, plan_doj, plan_amount, subtotal, 
        router_charge, installation_charge, 
        advance_planamount, advance_installationamount, advance_routeramount, discount, 
        balance_amount, advance_amount,
        total, plan_gst, router_gst, installation_gst} = this.state;
        
      if(!!customer_id && !!plan_name && !!plan_doj && !!plan_amount && !!subtotal && !!router_charge && !!installation_charge &&
         !!advance_planamount && !!advance_installationamount && !!advance_routeramount && !!discount && !!balance_amount &&
          !!advance_amount && !!total && !!plan_gst && !!router_gst && !!installation_gst){
        APISerivce.post('/addconnections',{
          userId: 1,
          customer_id, 
          plan_id: plan_name, 
          connection_date: plan_doj, 
          plan_amount, 
          plan_gst_id: plan_gst, 
          installation_gst_id: installation_gst, 
          router_gst_id: router_gst,
          subtotal, 
          router_charge, 
          installation_charge, 
          plan_gst, 
          router_gst, 
          installation_gst,
          advance_planamount, 
          advance_installationamount, 
          advance_routeramount, 
          discount, 
          balance_amount, 
          advance_amount,
          total, 
          status: 1
      }).then(()=>{
        this.props.history.push('/connections')
      }).catch(err=>{
        alert('Error Try again',err)
      })
      }else {
        alert("Please fill all the details");
      }
    }

    getChargeAmount = (lst, name, indx)=> {
      const inst = lst.find(val=>val.id === indx);
      const instln = !!inst ? inst[name]: 0
      return instln
    }

    totalCalculation = (install, plan, router) => {
      const { if_router_available } = this.state;
      if(if_router_available === false) {
        return parseInt(install) + parseInt(plan) + parseInt(router)
      }else {
        return parseInt(install) + parseInt(plan)
      }
    }

    getGrantTotal = ()=>{
      const {instllation_list, router_list, installation_charge, plan_amount, 
        router_charge, gst, gst_list, discount, if_router_available, 
        advance_planamount, advance_installationamount, advance_routeramount, 
        plan_gst, router_gst, installation_gst} = this.state;

      let plngstpercent = this.getChargeAmount(gst_list, 'gst_percent', plan_gst);
      let rutrgstpercent = this.getChargeAmount(gst_list, 'gst_percent', router_gst);
      let instlngstpercent = this.getChargeAmount(gst_list, 'gst_percent', installation_gst);

      const instln = this.getChargeAmount(instllation_list, 'installation_amount', installation_charge);
      const routr = this.getChargeAmount(router_list, 'router_amount', router_charge);

      let plnWthGst = !!plngstpercent ? (parseInt(plan_amount) / 100 ) * plngstpercent : 0;
      let rutrWthGst = !!rutrgstpercent ? (parseInt(routr) / 100 ) * rutrgstpercent : 0;
      let instlnWthGst = !!instlngstpercent ? (parseInt(instln) / 100 ) * instlngstpercent : 0;

      plnWthGst = parseInt(plnWthGst) + parseInt(plan_amount);
      rutrWthGst = parseInt(rutrWthGst) + parseInt(routr);
      instlnWthGst = parseInt(instlnWthGst) + parseInt(instln);
      console.log('plnWthGst',plnWthGst,rutrWthGst,instlnWthGst,plngstpercent,this.state)
      let subTotal = this.totalCalculation(instlnWthGst, plnWthGst, rutrWthGst)
      subTotal  = parseInt(subTotal);

      let discountval = (subTotal / 100 ) * parseInt(discount) ;
      discountval = subTotal - parseInt(discountval);
      discountval = parseInt(discountval);
      
      let gstpercent = this.getChargeAmount(gst_list, 'gst_percent', gst);

      let gstcountval = !!gst ? (discountval / 100) * parseInt(gstpercent)  : 0 ;
      gstcountval =  discountval + parseInt(gstcountval);
      gstcountval = parseInt(gstcountval)
      
      const advance_amount = parseInt(advance_planamount) + parseInt(advance_installationamount) + (if_router_available === false ? parseInt(advance_routeramount) : 0)
      this.setState({
        subtotal: subTotal,
        total: gstcountval,
        advance_amount,
        balance_amount: gstcountval - advance_amount
      })
    }

    handleUserChange = event => {
        const { name, value } = event.target;
        if(name === 'plan_name') {
          APISerivce.get(`/listplan/${value}`).then(res=>{
            const { plan_amount } = res.data;
            this.setState({
              [name]: value,
              plan_amount,
            },()=>{
              this.getGrantTotal()
            })
          }).catch(err=>{
            console.log("err",err)
          })
        }else if(name === 'if_router_available') {
          this.setState({
            [name]: event.target.checked ? true : false,
          },()=>{
            this.getGrantTotal()
          })
        } else {
          this.setState({
            [name]: value
          },()=>{
            this.getGrantTotal()
          })
        }
    }

    render() {
        const { customer_id, plan_id, plan_doj, plan_amount, gst_list, subtotal, 
          customer_list, plan_list, instllation_list, router_list,
          router_charge, installation_charge, 
          advance_planamount, advance_installationamount, advance_routeramount, discount, 
          balance_amount, advance_amount,
          total, if_router_available, plan_gst, router_gst, installation_gst } = this.state;
        return (
            <>
              <CRow>
                <CCol xs="12" md="10">
                  <CCard>
                    <CCardHeader>
                      Customer Add
                      <small> Form</small>
                    </CCardHeader>
                    <CCardBody>
                      <CForm action="" method="post" className="form-horizontal">
                      <CFormGroup row>
                          <CCol md="3">
                            <CLabel htmlFor="customer-name">Customer Name</CLabel>
                          </CCol>
                          <CCol xs="12" md="9">
                            <CSelect custom name="customer_id" onChange={this.handleUserChange} value={customer_id} id="customer-name">
                                <option value="">Please select Customer</option>
                                {
                                  customer_list && customer_list.map(val=>
                                  <option value={`${val.id}`} key={`conn_${val.id}`}>{`${val.customer_id} ${val.customer_name}`}</option>    
                                  )
                                }
                            </CSelect>
                          </CCol>
                        </CFormGroup>
                        <CFormGroup row>
                          <CCol md="3">
                            <CLabel htmlFor="plan-name">Plan Name</CLabel>
                          </CCol>
                          <CCol xs="12" md="9">
                            <CSelect custom name="plan_name" onChange={this.handleUserChange} value={plan_id} id="plan-name">
                                <option value="">Please select Plan</option>
                                {
                                  plan_list && plan_list.map(val=>
                                  <option value={`${val.id}`} key={`conn_${val.id}`}>{`${val.plan_id} ${val.plan_amount}`}</option>    
                                  )
                                }
                            </CSelect>
                          </CCol>
                        </CFormGroup>
                        <CFormGroup row>
                          <CCol md="3">
                            <CLabel htmlFor="plan-doj">Date</CLabel>
                          </CCol>
                          <CCol xs="12" md="9">
                            <CInput type="date" id="plan-doj" name="plan_doj" onChange={this.handleUserChange} value={plan_doj} placeholder="date" />
                          </CCol>
                        </CFormGroup>
                        <CFormGroup row>
                          <CCol xs="2">
                            <CFormGroup>
                              <CLabel htmlFor="plan-amount">Plan Amount</CLabel>
                              <CInput type="text" value={plan_amount} onChange={this.handleUserChange} id="plan-amount" name="plan_amount" placeholder="Plan Amount" />
                            </CFormGroup>
                          </CCol>
                          <CCol xs="2">
                            <CFormGroup>
                              <CLabel htmlFor="plan-gst">GST</CLabel>
                              <CSelect custom name="plan_gst" onChange={this.handleUserChange} value={plan_gst} id="plan-gst"  placeholder="GST">
                                <option value="">select GST</option>
                                {
                                  !!gst_list && gst_list.map(val=>
                                  <option value={`${val.id}`} key={`conn_${val.id}`}>{`${val.gst_percent}`}</option>    
                                  )
                                }
                            </CSelect>
                            </CFormGroup>
                          </CCol>
                          <CCol xs="2">
                            <CFormGroup>
                              <CLabel htmlFor="instllation-charge">Installation Charge</CLabel>
                              <CSelect custom onChange={this.handleUserChange} value={installation_charge} id="instllation-charge" name="installation_charge" placeholder="Instllation Charge">
                                <option value="">select Installation</option>
                                {
                                  !!instllation_list && instllation_list.map(val=>
                                  <option value={`${val.id}`} key={`conn_${val.id}`}>{`${val.installation_amount}`}</option>    
                                  )
                                }
                            </CSelect>
                            </CFormGroup>
                          </CCol>
                          <CCol xs="2">
                            <CFormGroup>
                              <CLabel htmlFor="installation-gst">GST</CLabel>
                              <CSelect custom name="installation_gst" onChange={this.handleUserChange} value={installation_gst} id="installation-gst"  placeholder="GST">
                                <option value="">select GST</option>
                                {
                                  !!gst_list && gst_list.map(val=>
                                  <option value={`${val.id}`} key={`conn_${val.id}`}>{`${val.gst_percent}`}</option>    
                                  )
                                }
                            </CSelect>
                            </CFormGroup>
                          </CCol>
                          <CCol xs="2">
                            <CFormGroup>
                              <CLabel htmlFor="router-charge">Router Charge</CLabel>
                              <CSelect custom name="router_charge" onChange={this.handleUserChange} value={router_charge} id="router-charge"  placeholder="Router Charge">
                                <option value="">select Router</option>
                                {
                                  !!router_list && router_list.map(val=>
                                  <option value={`${val.id}`} key={`conn_${val.id}`}>{`${val.router_amount}`}</option>    
                                  )
                                }
                            </CSelect>
                            </CFormGroup>
                          </CCol>
                          <CCol xs="2">
                            <CFormGroup>
                              <CLabel htmlFor="gst">GST</CLabel>
                              <CSelect custom name="router_gst" onChange={this.handleUserChange} value={router_gst} id="router-gst"  placeholder="GST">
                                <option value="">select GST</option>
                                {
                                  !!gst_list && gst_list.map(val=>
                                  <option value={`${val.id}`} key={`conn_${val.id}`}>{`${val.gst_percent}`}</option>    
                                  )
                                }
                            </CSelect>
                            </CFormGroup>
                          </CCol>
                        </CFormGroup>
                        <CFormGroup row>
                          <CCol xs="4">
                            <CFormGroup>
                              <CLabel htmlFor="pay-plan-amount">Plan Amount</CLabel>
                              <CInput type="text" value={advance_planamount} onChange={this.handleUserChange} id="pay-plan-amount" name="advance_planamount" placeholder="Plan Amount" />
                            </CFormGroup>
                          </CCol>
                          <CCol xs="4">
                            <CFormGroup>
                              <CLabel htmlFor="pay-instllation-charge">Advance Instllation</CLabel>
                              <CInput type="text" value={advance_installationamount} onChange={this.handleUserChange} id="pay-instllation-charge" name="advance_installationamount" placeholder="Instllation Charge" />
                            </CFormGroup>
                          </CCol>
                          <CCol xs="4">
                            <CFormGroup variant="checkbox" className="checkbox">
                              <CInputCheckbox id="pay-router-charge" name="if_router_available" onChange={this.handleUserChange} checked={if_router_available} />
                              <CLabel htmlFor="no-router-charge">No Router Charge</CLabel>
                            </CFormGroup>
                            <CFormGroup>
                              <CInput type="text" value={advance_routeramount} onChange={this.handleUserChange} id="pay-router-charge" name="advance_routeramount" placeholder="Router Charge" />
                            </CFormGroup>
                          </CCol>
                        </CFormGroup>
                        <CFormGroup row>
                          <CCol xs="4">
                            <CFormGroup>
                              <CLabel htmlFor="sub-total">SUBTOTAL</CLabel>
                              <CInput type="text" disabled={true} value={subtotal} onChange={this.handleUserChange} id="sub-total" name="subtotal" placeholder="sub-total" />
                            </CFormGroup>
                          </CCol>
                          <CCol xs="4">
                            <CFormGroup>
                              <CLabel htmlFor="discount">Discount</CLabel>
                              <CInput type="text" value={discount} onChange={this.handleUserChange} id="discount" name="discount" placeholder="Discount" />
                            </CFormGroup>
                          </CCol>
                        </CFormGroup>
                        <CFormGroup row>
                          <CCol md="3">
                            <CLabel htmlFor="total">Total</CLabel>
                          </CCol>
                          <CCol xs="12" md="9">
                            <CInput type="text" value={total} disabled={true} onChange={this.handleUserChange} id="total" name="total" placeholder="Total" />
                          </CCol>
                        </CFormGroup>        
                        <CFormGroup row>
                          <CCol md="3">
                            <CLabel htmlFor="paid-amount">Advance Amount</CLabel>
                          </CCol>
                          <CCol xs="12" md="9">
                          <CInput type="text" value={advance_amount} disabled={true} onChange={this.handleUserChange} id="paid-amount" name="advance_amount" placeholder="Paid" />
                          </CCol>
                        </CFormGroup>
                        <CFormGroup row>
                          <CCol md="3">
                            <CLabel htmlFor="balance-amount">Balance</CLabel>
                          </CCol>
                          <CCol xs="12" md="9">
                            <CInput type="text" value={balance_amount} disabled={true} onChange={this.handleUserChange} id="balance-amount" name="balance_amount" placeholder="Bill" />
                          </CCol>
                        </CFormGroup>
                      </CForm>
                    </CCardBody>
                    <CCardFooter>
                      <CButton type="reset" size="sm" color="danger" onClick={this.handleCancel}><CIcon name="cil-ban"  /> Cancel</CButton>
                      &nbsp;
                      <CButton type="submit" size="sm" color="primary" onClick={this.handleSubmit}><CIcon name="cil-scrubber" /> Add</CButton> 
                    </CCardFooter>
                  </CCard>
                </CCol>
              </CRow>
            </>
          )
    }
}

export default BasicForms
